.custom-navbar {
    background: rgba(51, 51, 51, 0.7); /* Semi-transparent dark background */
    backdrop-filter: blur(10px); /* Apply blur effect */
    border-radius: 10px; /* Rounded corners */
    padding: 10px 15px;
    max-width: 600px; /* Adjust width for desktop */
    margin: 0 auto;
    position: fixed;
    bottom: 20px; /* Position it at the bottom with some spacing */
    left: 0;
    right: 0;
    z-index: 10;
    box-sizing: border-box;
    display: flex; /* Flexbox to align items */
    transition: opacity 0.3s ease, transform 0.3s ease; /* Smooth transitions */
    opacity: 0; /* Start hidden */
    transform: translateY(-20px); /* Slightly move it up */
    justify-content: space-between; /* Distribute items evenly */
    
  }

.custom-navbar.show {
  opacity: 1; /* Make it visible */
  transform: translateY(0); /* Move it to its original position */
}

.custom-navbar.hidden {
  opacity: 0; /* Hide it */
  pointer-events: auto; /* Prevent interaction */
}

  .custom-navbar .logo {
    display: flex;
    align-items: center;
  }

  .custom-navbar .logo img {
    height: 40px; /* Adjust height as needed */
    margin-right: 10px;
    border-radius: 8px;
  }

  .custom-navbar .navbar-brand {
    background-color: #000; /* Black background for the logo */
    color: #fff; /* White text for the logo */
    padding: 5px 10px;
    border-radius: 10px;
  }

  .custom-navbar .navbar-nav .nav-link {
    background-color: #444; /* Dark background for nav links */
    color: #fff; /* White text */
    padding: 5px 15px;
    margin-right: 5px;
    border-radius: 5px;
  }

  .custom-navbar .navbar-nav .nav-link:hover {
    background-color: #555; /* Slightly lighter on hover */
    color: #fff;
  }

  .custom-navbar .navbar-nav .nav-link.active {
    background-color: #fff; /* White background for active link */
    color: #333; /* Dark text for active link */
  }

  .custom-navbar .navbar-toggler {
    border-color: #fff;
  }

  .custom-navbar .navbar-toggler-icon {
    background-color: #fff;
  }

  @media (max-width: 768px) {
    .custom-navbar {
      max-width: 95%; /* Full width on mobile */
      padding: 10px 10px; /* Adjust padding for mobile */
      display: none;


    }

    .custom-navbar .navbar-nav .nav-link {
      padding: 5px 10px;
      font-size: 0.9rem; /* Slightly smaller font on mobile */
    }

  }
  .custom-navbar .button-link {
    background-color: #444; /* Dark background for buttons */
    color: #fff; /* White text */
    padding: 5px 15px;
    margin-right: 5px;
    border-radius: 5px;
    border: none; /* Remove button borders */
    cursor: pointer; /* Change cursor to pointer */
    font-size: 1rem; /* Adjust font size */
  }
  
  .custom-navbar .button-link:hover {
    background-color: #555; /* Slightly lighter on hover */
  }
  
  .custom-navbar .button-link.active {
    background-color: #fff; /* White background for active button */
    color: #333; /* Dark text for active button */
  }
  
  /* Responsive styling */
  @media (max-width: 768px) {
    .custom-navbar .button-link {
      padding: 5px 10px;
      font-size: 0.9rem; /* Smaller font on mobile */
    }
  }
  
  /* Hide the navbar for screens smaller than 992px (mobile, tablets, iPads) */
@media (max-width: 991px) {
  .custom-navbar {
    display: none;
  }
}